import React from 'react';
import { observer } from 'mobx-react';
import { Link } from 'react-router-dom';
import { InputItem, Button } from 'antd-mobile';
// 
import { NavBar } from '@com';
// less
import './index.less';

// 
@observer
class Index extends React.Component {

    render() {
        const {
            form: { getFieldProps },
            handleTarget, loginSubmit, errTip
        } = this.props;
        return (
            <div className='dm_Logins'>
                <NavBar {...this.props} title='' 
                    onLeftClick={() => {
                        this.props.history.replace('/views/home');
                    }}
                />
                <form className='main_content'>
                    <InputItem
                        {...getFieldProps('uname', {
                            rules: [{
                                required: true,
                                message: ''
                            }]
                        })}
                        placeholder=""
                        clear
                    />
                    <InputItem
                        {...getFieldProps('upwd', {
                            rules: [{
                                required: true,
                                message: ''
                            }]
                        })}
                        type="password"
                        placeholder=""
                        clear
                    />
                    <div className='err_tip'>
                        { errTip.length ? errTip.join('')+'' : null }
                    </div>
                    <Button type='primary' className='login_btns' onClick={ loginSubmit }></Button>
                    <div className='login_handle'>
                        <a onClick={ handleTarget.bind(this, 'forget') }></a>
                        <Link to="/register"></Link>
                    </div>
                </form>
            </div>
        );
    }
}

export default Index;