import React from 'react';
import { observer } from 'mobx-react';
import { Link } from 'react-router-dom';
import { InputItem, Button } from 'antd-mobile';
import { createForm } from 'rc-form';
// 
import { NavBar } from '@com';
// 
import { indexState, PWD_KEY } from '@config';
// 
import state from './state';
// less
import './index.less';
// 
@observer
class Index extends React.Component {

    componentDidMount() {
        this.props.history && state.setHistory( this.props.history );
        indexState.oauthData();
    }

    // 
    handleSubmit = () => {
        this.props.form.validateFields((err, values) => {
            if (!err) {
                const { upwd, confirm } = values || {};
                if( upwd != confirm ) {                        
                    state.setErrTip(['']);
                }else{
                    state.setErrTip();
                    values.upwd = this.$md5( values.upwd + PWD_KEY );
                    values.confirm = this.$md5( values.confirm + PWD_KEY );
                    state.registerData( values );
                }
            }else{
                let errTip = [];
                for(let [key, value] of Object.entries(err)) {
                    const { errors } = value || {};
                    if(errors) {
                        const { message } = (errors && errors[0]) || {};
                        errTip.push(message);
                    }
                }
                state.setErrTip(errTip);
            }
        });
    };

    componentWillUnmount() {
        state.clearMobxData();
    }

    render() {
        const { getFieldProps } = this.props.form;
        const { errTip } = state;
        return (
            <div className='dm_Register'>
                <NavBar {...this.props} title='' 
                    onLeftClick={() => {
                        this.props.history.replace('/login');
                    }}
                />
                <form className='main_content'>
                    <InputItem
                        {...getFieldProps('uname', {
                            rules: [{
                                required: true,
                                message: ''
                            }]
                        })}
                        placeholder=""
                        clear
                    ></InputItem>
                    <InputItem
                        {...getFieldProps('phone', {
                            rules: [{
                                required: true,
                                message: ''
                            }]
                        })}
                        placeholder=""
                        clear
                        type="phone"
                    ></InputItem>
                    <InputItem
                        {...getFieldProps('email', {
                            rules: [{
                                required: true,
                                message: ''
                            }]
                        })}
                        placeholder=""
                        clear
                        type='email'
                    ></InputItem>
                    <InputItem
                        {...getFieldProps('upwd', {
                            rules: [{
                                required: true,
                                message: ''
                            }]
                        })}
                        placeholder=""
                        clear
                        type="password"
                    ></InputItem>
                    <InputItem
                        {...getFieldProps('confirm', {
                            rules: [{
                                required: true,
                                message: ''
                            }]
                        })}
                        placeholder=""
                        clear
                        type="password"
                    ></InputItem>
                    <div className='err_tip'>
                        { errTip.length ? errTip.join('')+'' : null }
                    </div>
                    <Button type='primary' className='login_btns' onClick={ this.handleSubmit }></Button>
                    <div className='login_handle'>
                        <Link to="/login"></Link>
                    </div>
                </form>
            </div>
        );
    }
}

export default createForm()(Index);