import React from 'react';
import { InputItem, List, Picker } from 'antd-mobile';
import { createForm  } from 'rc-form';
import { toJS } from 'mobx';
import { observer } from 'mobx-react';
// 
import { formUtils } from '@utils';

const onFieldsChange = (props, changedFields) => {
    props.setAddressModalData({...toJS( props.addressModalData ), ...formUtils.formToMobx(changedFields)});
};
const mapPropsToFields = (props) => {
    if( toJS( props.addressModalData ) ){
        return formUtils.mobxToForm({...toJS( props.addressModalData )});
    }
};

const isDefaultArr = [
    {
        label: '',
        value: 1
    },
    {
        label: '',
        value: 0
    }
];

// 
@observer
class Index extends React.Component {

    componentDidMount() {
        this.props.setForm && this.props.setForm(this.props.form);
    }

    render() {
        const { getFieldProps } = this.props.form;
        const { errTip=[] } = this.props;
        return (
            <form>
                <InputItem 
                    {...getFieldProps('name', {
                        rules: [{
                            required: true,
                            whitespace: true,
                            message: '' 
                        }]
                    })}
                    placeholder=''
                ></InputItem>
                <InputItem 
                    {...getFieldProps('region', {
                        rules: [{
                            required: true,
                            whitespace: true,
                            message: '' 
                        }]
                    })}
                    placeholder=''
                ></InputItem>
                <InputItem 
                    {...getFieldProps('detail', {
                        rules: [{
                            required: true,
                            whitespace: true,
                            message: '' 
                        }]
                    })}
                    placeholder=''
                ></InputItem>
                <InputItem 
                    {...getFieldProps('phone', {
                        rules: [{
                            required: true,
                            whitespace: true,
                            message: '' 
                        }]
                    })}
                    placeholder=''
                    type='phone'
                ></InputItem>
                <Picker data={ isDefaultArr } cols={ 1 } 
                    {...getFieldProps('isDefault', {
                        rules: [{
                            required: true, 
                            message: '' 
                        }]
                    })}
                >
                    <List.Item arrow="horizontal"></List.Item>
                </Picker>
                <div className='err_tip'>
                    { errTip.length ? errTip.join('') : null }
                </div>
            </form>
        );
    }
}

export default createForm({ 
    onFieldsChange, 
    mapPropsToFields 
})(Index);