import React from 'react';
import { InputItem, Button } from 'antd-mobile';
import { observer } from 'mobx-react';
// 
import { NavBar } from '@com';
// less
import './index.less';

// 
@observer
class Index extends React.Component {
    render() {
        const {
            form: { getFieldProps },
            handleTarget, errTip
        } = this.props;
        return (
            <div className='dm_NewPassword'>
                <NavBar {...this.props} title='' 
                    onLeftClick={ handleTarget }
                />
                <form className='main_content'>
                    <InputItem
                        {...getFieldProps('uPwd', {
                            rules: [{
                                required: true,
                                message: ''
                            }]
                        })}
                        placeholder=""
                        clear
                    ></InputItem>
                    <InputItem
                        {...getFieldProps('confirm', {
                            rules: [{
                                required: true,
                                message: ''
                            }]
                        })}
                        placeholder=""
                        clear
                    ></InputItem>
                    <div className='err_tip'>
                        { errTip.length ? errTip.join('') : null }
                    </div>
                    <Button type="primary" className='login_btns' onClick={ handleTarget.bind(this, 'log') }></Button>
                    <div className='login_handle'>
                        <a onClick={ handleTarget }></a>
                    </div>
                </form>
            </div>
        );
    }
}

export default Index;